import { createRouter, createWebHistory } from 'vue-router'
const routes = [
 
  {
    path: '/',
     redirect: '/sun'
  },
  {
    path: '/kg',
    name: 'kg',
    // component: () => import(/* webpackChunkName: "about" */ '../components/Kg4Qa/index.vue')
    component: () => import(/* webpackChunkName: "about" */ '../components/kg/KGView.vue')
  },
  {
    path: '/sun',
    name: 'sun',
    component: () => import(/* webpackChunkName: "about" */ '../components/sun/SunView.vue')
  },
  {
    path: '/galexy',
    name: 'galexy',
    component: () => import(/* webpackChunkName: "about" */ '../components/galexy/GalexyView.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
