<template>
    <div class="row" id="box" v-show="llmShowFlag" :style="'z-index:'+ layer+';'">

        <el-row>
            <el-col :span="8">
                <!-- 聊天记录边框开始 -->
                <div class="col-xs-2 col-lg-2 col-sm-2 historyView" style="box-shadow: #e2e2e2 0px 0px 1px 1px;" id="historyView">
                    <button type="button" class="btn btn-default" @click="chatCreate" style="width: 80%;border-style: dashed;" id="createNewChat">新聊天</button>
                    <hr>
                    <div class="itemList">
                        <button type="button" class="btn btn-default" @click="show($event)"  style="width: 80%;display: none;" id="newChat">新聊天</button>
                        <button type="button" class="btn btn-default showBtn" @click="show($event)" style="width: 80%;" id="newChat0" index="0">新聊天</button>

                    </div>
                   
                </div>
                <!-- 聊天记录边框结束 -->

            </el-col>
            <el-col :span="16">
                 <!--聊天开始-->
               
        <div class="chatView" style="box-shadow: #e2e2e2 0px 0px 1px 1px;" id="chatView">
            
                <div class="row chatBox" style="background:#000;">
                    <div class="row conversation" style="margin: 0;overflow: auto;border-bottom: 1px solid #bcbcbc;display: none" id="conversation">
                        <!--用户消息气泡开始-->
                        <div class="row col-xs-12" id="user-message" style="display:none;">
                            <div class="user-message">
                                <img src="../../assets/用户.svg" alt="" class="headImg">
                                <p class="dialog text"></p>
                                
                            </div>
                        </div>
                        <!--用户消息气泡结束-->
                        <!--robot消息气泡开始-->
                        <div class="row col-xs-12" id="robot-message" style="display:none;">
                            <div class="robot-message">
                                <img src="../../assets/robot.png" alt="" class="headImg">
                                <p class="dialog text"></p>
                              
                            </div>
                        </div>
    
    
                        <!-- robot消息气泡结束-->
    
                    </div>
                    <!--聊天消息框开始-->
                    <div class="row conversation" style="margin: 0;overflow: auto;border-bottom: 1px solid #bcbcbc" id="conversation0">

                    </div>
                    <!--聊天消息框结束-->
                    
                    <!--聊天输入框开始-->
                    <div class="mt-4" id="question-total">
                        <el-row>
                            <el-col :span="20" ><el-input style="background-color: black;"  v-model="input" placeholder="Please input" id="question-input"  class="custom-input"></el-input></el-col>
                            <el-col  :span="4"><div  @click="msgSend" id="questionsub"><el-icon   ><Promotion /></el-icon></div></el-col>
                        </el-row>
                        <!-- <el-input  v-model="input" placeholder="Please input">
                            
                          <template #append >  </template>
                        </el-input> -->
                       
                      </div>
                       
    
                </div>
                <!--聊天主体开始-->
            </div>
    
      
            </el-col>
        </el-row>
 
    </div>

</template>

<script  setup>

import { defineProps } from 'vue';
import {  ref } from 'vue';

 defineProps({
  llmShowFlag:Boolean,
  layer:Number
})
// import _ from "lodash";
const chatViewShow = ref("0")
const input = ref("")
// const list = reactive([])
let i =1;
const show =(e) =>{
    console.log(e)
    // console.log(e)
    let index = e.target.index || e.target.attributes.index.value;
    // console.log(e.target.attributes.index.value)
    styleChange(index)
   chatViewShow.value = index
}



const msgSend=() => {
    // var questionInput = document.querySelector("#question");

    var question = input.value;
    if (question === "") {
        alert("Message cannot be empty");
        return;
    } else {
        addMessage(question, "user", chatViewShow.value);
        MeadicalQA(question,chatViewShow.value)

        input.value = ""; // 清空输入框的内容
        var conversation = document.getElementById("conversation" + chatViewShow.value);
        conversation.scrollTop = conversation.scrollHeight - conversation.offsetHeight;
        return;
    }
}


const MeadicalQA=(question, index) => {
   
    var xhr = new XMLHttpRequest();
    var url = "https://aistudio.baidu.com/bd-cpu-02/user/7671230/8179580/api_serveing/3389/getanswer"
    var data = {
        "query":question,
       
    };

    xhr.open("POST", url, true);
    xhr.setRequestHeader("Content-type", "application/json");
    
    xhr.addEventListener("load", function() {
        if (xhr.status >= 200 && xhr.status < 300) {
            // 请求成功
            var response = JSON.parse(xhr.responseText);
            addMessage(response.data, "robot", index);
            console.log("请求成功", response);
        } else {
            // 请求失败
            console.log("请求失败", xhr.statusText);
        }
    });

    xhr.addEventListener("error", function() {
        // 请求失败
        console.log("请求失败");
    });
    
    xhr.send(JSON.stringify(data)); // 将数据对象转换为JSON字符串并发送

}





const styleChange=(index) => {
    console.log(chatViewShow.value, "show");
    console.log(index, "newshow");
    document.getElementById("newChat" + chatViewShow.value).classList.remove("showBtn");
    // console.log(document.getElementById("newChat" + index).classList);
    document.getElementById("newChat" + index).classList.add("showBtn");
    document.getElementById("conversation" + chatViewShow.value).style.display = "none";
    document.getElementById("conversation" + index).style.display = "";


}


// 新建一个chatView
const chatCreate= () => {

    // var historyView = document.getElementById("chatView");

    // 新建一个按钮
    // _.cloneDeep(document.getElementById("newChat"))
    // console.log(newChatButton)
    var newChatButton = document.getElementById("newChat").cloneNode(true);
   
    newChatButton.style.display = "";
    newChatButton.setAttribute("id", "newChat" + i)
    newChatButton.setAttribute("index", i)
    newChatButton.index = i;
    newChatButton.addEventListener('click', (event) => {
        show(event)
        // 在这里可以编写你想要执行的逻辑
      });


    // 新建一个聊天界面
    var newChatView = document.getElementById("conversation").cloneNode(true);
    // newChatView.querySelector("#user-message").setAttribute("id", "user-message" + i)

    // newChatView.querySelector("#robot-message").setAttribute("id", "robot-message" + i)

    // newChatView.querySelector("#conversation").setAttribute("id", "conversation" + i)
    newChatView.setAttribute("id", "conversation" + i);
   
    // newChatView.style.display = "";
    // document.getElementById("chatView" + localStorage.getItem("chatViewShow.value")).style.display = "none";
    // historyView.insertBefore(newChatButton, document.getElementById("newChat"));
    document.getElementById("newChat").insertAdjacentElement('afterend', newChatButton);
    document.getElementById("conversation0").insertAdjacentElement('afterend', newChatView);
    styleChange(i);
    chatViewShow.value = i++
    



}
// 窗口中添加新信息
const addMessage=(message, role, index)  =>{
    
    let showBtn = document.getElementById("newChat" + chatViewShow.value)
    console.log(showBtn)
    console.log(showBtn.innerHTML)
    if (showBtn.innerHTML == "新聊天" && role == "user") {
        showBtn.innerHTML = message
    }
    // if ()
    // alert(index)
    // message字符串
    // role包含user和robot两个
    var Conversation = document.getElementById("conversation" + index);
    var userMessage = document.getElementById(role + '-message');

    var clonedUserMessage = userMessage.cloneNode(true);
    clonedUserMessage.setAttribute("id", "")
    clonedUserMessage.style.display = ""
    console.log(clonedUserMessage.style, role + "message")
    clonedUserMessage.querySelectorAll("p")[0].innerHTML = message;
    Conversation.appendChild(clonedUserMessage);
}


</script>
<style scoped >
@import url("./chat.less");
</style>